@font-face {
  font-family: 'Hate Your Writing';
  src: url('fonts/HateYourWriting.eot');
  src: local('HateYourWriting'),
      url('fonts/HateYourWriting.eot?#iefix') format('embedded-opentype'),
      url('fonts/HateYourWriting.woff2') format('woff2'),
      url('fonts/HateYourWriting.woff') format('woff'),
      url('fonts/HateYourWriting.ttf') format('truetype'),
      url('fonts/HateYourWriting.svg#HateYourWriting') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

