html, body, #root {
  height: 100%;
}

.App, .Content {
  min-height: 100%;
}

.Content {
  height: 100%;
}

.App{
  display: grid;
  grid-template-rows: 100%;
}

.Content {
  display : flex;
  flex-direction: column;
  flex-wrap: nowrap;
  grid-gap: 10px;
  gap: 10px;
  min-height: 760px;
  pointer-events: none;
}

.Browser {
  flex: 1 1;
  margin-top: 20px;
  margin-bottom: 20px;
}

.Footer {
  display:flex;
  flex-direction: column;
  flex-wrap: nowrap;
  grid-row-gap: 10px;
  row-gap: 10px;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.footerText {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
}

.footerIcons {
  flex: 1 1;
  min-height: 40px;
  display: flex;
  flex-direction: row;
  flex-flow: nowrap;
  grid-gap:20px;
  gap:20px;
}

.footerIcon {
  height: 45px;
  width: 45px;
  pointer-events: auto;
}

.footerIcon>a, .footerIcon>a>img {
  width: 100%;
  height: 100%;
}

