@tailwind base;
@tailwind utilities;
@tailwind components;
@tailwind variants;
.About {
    min-height: 100%;
}

.aboutWrap {
    display: flex;
    flex-direction: column;
    align-content: space-around;
    gap: 40px;
    margin-top: 80px;
    margin-bottom: 40px;
 }
.aboutHeader {
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
}
.aboutTitle {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.aboutTitleText {
    @apply mx-4;
}
.aboutTitleButton {
    width: 100%;
    max-width: 100px;
    @apply hover:text-brightTurquoise duration-200;
    @apply pointer-events-auto;
}
.aboutTitleButton>a {
    display: flex;
    align-items: center;
    justify-content: center;
    @apply pointer-events-auto;
}
.aboutArrow {
}
.aboutArrowReverse {
    transform: scale(-1,1);
}
.aboutResume {
    display: flex;
    flex-direction: row;
    justify-content: center;
    @apply pointer-events-auto;
}
.aboutResumeText {
    @apply duration-200 px-5 py-1 border-solid border-4 border-paleTurquoise rounded-lg hover:text-brightTurquoise hover:border-dotted hover:border-brightTurquoise;
}
.aboutCards {
    margin-bottom: 100px;
}
.aboutCardsCanvas {
    display: grid;
    justify-items: center;
    margin-top: 100px;
    gap: 20px;
    
}

.aboutCard {
    width: 100%;
    max-width: 300px;
    @apply pointer-events-auto;
}

.cardContent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @apply bg-darkestBlue rounded-md;
}
.cardContentHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    
}
.cardContentHeaderBar {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 5px;
    width: 100%;
    @apply bg-paleTurquoise rounded-t-lg;
}
.cardContentHeaderIcon {
    position: absolute;
    height: 72px;
    width: 72px;
    transform: translate(0%,-70%);
    display: flex;
    align-items: center;
    justify-content: center;
    @apply bg-darkestBlue rounded-full border-3 border-solid border-paleTurquoise;
}
.cardContentHeaderIcon>img {
    width: 100%;
}
.cardContentTitle {
    @apply mt-6 mb-2;
}
.cardContentText {
    @apply mx-7 my-6 text-white text-justify;
}
.cardContentFooter {
    height: 5px;
    @apply bg-paleTurquoise rounded-b-lg;
}

.aboutProjects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 150px;
}
.aboutProjectsTitle {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.aboutProjectsGithub {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.aboutProjectsGithubFiller {
    width: 64px;
    margin-right: 10px;
}
.aboutProjectsGithubText {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 460px;
}
.aboutProjectsGithubIcon {
    width: 100%;
    max-width: 100px;
    display: flex;
    height: 64px;
    width: 64px;
    margin-left: 10px;
    justify-content: center;
    align-items: center;
    @apply pointer-events-auto;
}
.aboutProjectsGithubIcon>a {
    height: 64px;
    width: 64px;;
}
.aboutProjectsGithubIcon>a>img:hover {
    filter: brightness(0%) saturate(100%) invert(80%) sepia(14%) saturate(6779%) hue-rotate(113deg) brightness(96%) contrast(77%);
}

.aboutProjectCards {
    display: grid;
    justify-items: center;
    width: 100%;
    max-width: 1100px;
    column-gap: 20px;
    row-gap: 100px;
}
.projectCard {
    height: 100%;
    width: 100%;
    max-width: 350px;
    @apply bg-darkestBlue rounded-lg pointer-events-auto;
}
.projectCardContent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: flex-start;
    align-items: flex-start;
    row-gap: 30px;
    height: 100%;
    @apply mx-6 my-2 text-white;
}
.projectCardHeader {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 20px;
}
.projectCardHeaderTop {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}
.projectCardHeaderFiller {
    width: 100%;
}
.projectCardHeaderIcon {
    position: absolute;
    transform: translate(0%, -80%);
    height: 64px;
    width: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.projectCardLinkContainer {
    width:100%;
    display:flex;
    flex-direction: row;
    justify-content: flex-end;
}
.projectCardLink {
    margin-right: -8px;
    margin-top: 8px;
    width: 24px;
    height: 24px;
}
.projectCardDescription {
    @apply text-justify;
}

/* FHD Onwards */
@media (min-width: 1920px) {
    .aboutCardsCanvas {
        column-gap: 200px;
    }
}
/* FHD Screens */
@media (min-width: 1600px) and (max-width: 1920px) {
    .aboutCardsCanvas {
        column-gap: 150px;
        }
    }
    
@media (min-width: 1280px) and (max-width: 1599px) {
.aboutCardsCanvas {
    column-gap: 120px;
    }
}

/* Bigger than tablet screens */
@media (min-width: 1280px) {
    .aboutCardsCanvas {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 'left right';
        /* row-gap: 200px; */
    }
    .area-first {
        justify-self: end;
    }
    .area-left {
        justify-self: end;
        margin-top: -250px;
    }
    .area-right {
        justify-self: start;
        margin-top: 350px;
    }
}
@media (max-width: 1280px) {
    .aboutCardsCanvas {
        grid-template-columns: 1fr;
        row-gap: 120px;
    }
    .aboutCardsCanvas {
        padding-top: 100px;
    }
    
}
/* Tablet */
@media (min-width: 768px) and (max-width: 1024px) {
    .aboutCard {
    }
}
/* Bigger Phone to tablet */
@media (min-width: 560px) and (max-width: 768px) {
    .projectCard {
        max-width: 300px;
    }
}
/* Anything smaller than a regular phone */

@media (max-width: 560px) {
    .projectCard {
        max-width: 270px;
    }
}

@media (min-width: 1600px) {
    .aboutProjectCards {
        grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    }
}
@media (min-width: 900px) and (max-width: 1600px) {
    .aboutProjectCards {
        grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    max-width: 750px;
    }
}
@media (max-width: 900px) {
    .aboutProjectCards {
        grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    }
}