@tailwind base;
@tailwind components;
@tailwind utilities;


html, body, #root {
    height: 100%;
}
.App, .Content {
    min-height: 100%;
}
.Content {
    height: 100%;
}
.App{
    display: grid;
    grid-template-rows: 100%;
}

.Content {
    display : flex;
    flex-direction: column;
    flex-wrap: nowrap;
    gap: 10px;
    min-height: 760px;
    @apply pointer-events-none;
}

.Browser {
    flex: 1;   
    margin-top: 20px;
    margin-bottom: 20px; 
}
.Footer {
    display:flex;
    flex-direction: column;
    flex-wrap: nowrap;
    row-gap: 10px;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}
.footerText {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40px;
}
.footerIcons {
    flex: 1;
    min-height: 40px;
    display: flex;
    flex-direction: row;
    flex-flow: nowrap;
    gap:20px;
    
}
.footerIcon {
    height: 45px;
    width: 45px;
    @apply pointer-events-auto;
}
.footerIcon>a, .footerIcon>a>img {
    width: 100%;
    height: 100%;
}
.filter-white {
    filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(228deg) brightness(102%) contrast(102%);
}
.filter-white:hover {
    /* paleTurquoise */
    @apply duration-200;
    filter: invert(87%) sepia(50%) saturate(701%) hue-rotate(66deg) brightness(96%) contrast(81%);
}
.filter-paleTurquoise {
    filter: invert(87%) sepia(50%) saturate(701%) hue-rotate(66deg) brightness(96%) contrast(81%);
}
.filter-cBlue {
    filter: invert(89%) sepia(10%) saturate(999%) hue-rotate(161deg) brightness(97%) contrast(86%);
}
.filter-cssBlue {
    filter: invert(68%) sepia(17%) saturate(5030%) hue-rotate(178deg) brightness(86%) contrast(89%);
}
.filter-pythonBlue {
    filter: invert(58%) sepia(49%) saturate(265%) hue-rotate(136deg) brightness(97%) contrast(88%);
}
.filter-reactBlue {
    filter: invert(64%) sepia(14%) saturate(6808%) hue-rotate(149deg) brightness(106%) contrast(105%);
}
.filter-octaveAqua {
    filter: invert(26%) sepia(66%) saturate(5640%) hue-rotate(166deg) brightness(86%) contrast(85%);
}
.filter-javascriptYellow {
    filter: invert(76%) sepia(32%) saturate(502%) hue-rotate(1deg) brightness(107%) contrast(97%);
}
.filter-htmlRed {
    filter: invert(55%) sepia(60%) saturate(3517%) hue-rotate(324deg) brightness(94%) contrast(102%);
}

/* FHD onwards */ 
@media (min-width: 1920px) {
    .App {
       grid-template-columns: auto 1400px auto;
    }
}
/* FHD screens */
@media (min-width: 1280px) and (max-width: 1920px) {
    .App {
       grid-template-columns: auto 65% auto;
    }
}
/* HD desktop */
@media (min-width: 1024px) and (max-width: 1280px) {
    .App {
       grid-template-columns: auto 70% auto;
    }
}
/* Tablets */
@media (min-width: 768px) and (max-width: 1024px) {
    .App {
       grid-template-columns: auto 80% auto;
    }
    
}
/* Bigger Phones */
@media (min-width: 480px) and (max-width: 768px) {
    .App {
       grid-template-columns: auto 80% auto;
    }
    .footerIcons {
        gap: 10px;
    }
    .footerIcon {
        height: 40px;
        width: 40px;
    }

}
/* Small Phones */
@media (max-width: 480px) {
    .App {
       grid-template-columns: auto 90% auto;
    }
    .footerIcons {
        gap: 10px;
    }
    .footerIcon {
        height: 35px;
        width: 35px;
    }
}