@tailwind base;
@tailwind utilities;
@tailwind components;
@tailwind variants;
.MainPage, .mainContent {
   min-height: 100%;
   height: 100%;
}
.mainContent {
   display: flex;
   flex-direction: column;
   justify-content: center; 
   align-items: center;
   align-content: center;
   width: 100%;

   gap: 20px;
   
}
.mainHeader {
   flex: 2;
   display:flex;
   flex-direction: column;
   justify-content: center;
   width: 100%;
}
.mainHeader>div {
   padding: 10px;
}
.mainSubheader {
   flex: 4;
   width: 100%;
   display: grid;
   grid-template-columns: 100%;
   grid-template-rows: repeat(5, 20%);
}
.mainSubheader>div {
   grid-row: 2/last-line;
   padding: 10px;
}
.mainButtonsSection {
   padding: 20px;
   width: 100%;
   display: flex;
   flex-flow: wrap;
   justify-content: center;
   align-items: center;
   max-width: 600px;
   row-gap: 20px;
   column-gap: 120px;
}
.mainButton {
   width: 180px;
   height: 50px;
   display: flex;
   flex-flow: row wrap;
   
   justify-content: center;
   align-items: center;
   align-content: center;
   @apply border-solid border-paleTurquoise border-4 rounded-2xl duration-300 hover:bg-paleTurquoise;
}
.mainButton>a {
   width: 100%;
   height: 100%;
   flex: 1;
   display: flex;
   align-items: center;
   justify-content: center;
   @apply pointer-events-auto;
}

/* FHD onwards */ 
@media (min-width: 1920px) {
   
}
/* FHD screens */
@media (min-width: 1280px) and (max-width: 1919px) {
   .mainButton {
      width: 170px;
   }
}
/* HD desktop */
@media (min-width: 1024px) and (max-width: 1279px) {
   .mainButton {
      width: 170px;
   }
}
/* Tablets */
@media (min-width: 768px) and (max-width: 1023px) {
   .mainButton {
      width: 170px;
   }
}
/* Bigger Phones */
@media (min-width: 480px) and (max-width: 767px) {
   .mainButton {
      width: 170px;
   }
}
/* Small Phones */
@media (max-width: 479px) {
   .mainButton {
      width: 160px;
   }
}