@tailwind base;
@tailwind utilities;
@tailwind components;
@tailwind variants;
.Blog {
    min-height: 100%;
}

.blogWrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    margin-bottom: 40px;
    gap: 80px;
 }
 .blogTitle {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
   align-items: center;
}
.blogTitleText {
    @apply mx-4;
    
}
.smalltxt {
    position: absolute;
    z-index: 10;
}

.blogTitleButton {
    width: 100%;
    max-width: 100px;
    @apply hover:text-brightTurquoise duration-200;
    @apply pointer-events-auto;
}
.blogTitleButton>a {
    display: flex;
    align-items: center;
    justify-content: center;
}
.blogArrow {
}
.blogArrowReverse {
    transform: scale(-1,1);
}
.blogCards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 100%;
    max-width: 1100px;
    gap: 10px;
}
.blogCard {
    height: 100%;
    width: 100%;
    max-width: 300px;
    @apply bg-darkestBlue rounded-md pointer-events-auto;
    
}
.blogCardContent {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
}
.blogCardHeader {
    display:flex;
    flex-direction: column;
}
.blogCardHeaderLine {
    height: 5px;
    @apply rounded-t-md;
}
.blogCardTitle {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.blogCardFooter {
    height: 5px;
    @apply rounded-b-md;
}
.blogCardHeaderPhoto {
    height: 160px;
    @apply bg-lightestGray object-cover;
    margin-bottom: 6px;
}
.blogCardHeaderDate {
    @apply p-2;
    align-self: center;
}
.blogCardDescription {
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
    @apply mx-7 my-6 text-justify;
}