@tailwind base;
@tailwind utilities;
@tailwind components;

.header-text {
  @apply font-poppins font-extrabold text-6xl text-paleTurquoise text-center;
}
.subheader-text {
  @apply font-winkle font-normal text-4xl text-white text-center;
}
.mainbutton-text {
  @apply font-poppins font-bold text-xl text-paleTurquoise hover:text-black text-center;
}
.mainbutton-text:hover {
  @apply mix-blend-screen;
}
.footerText {
  @apply font-babydoll font-semibold text-2xl text-white;
}

.aboutTitleText {
  @apply font-hateyourwriting text-8xl text-paleTurquoise tracking-wide font-normal text-center;
}
.aboutTitleBackButton {
  @apply text-3xl;
}
.aboutResume {
  @apply font-winkle font-medium text-2xl text-paleTurquoise;
}
.aboutProjectsTitleText {
  @apply font-hateyourwriting text-8xl text-paleTurquoise;
}
.aboutProjectsGithubText {
  @apply font-hateyourwriting text-5xl text-brightTurquoise;
}


.cardContentTitle {
  @apply text-5xl text-paleTurquoise font-hateyourwriting;
}


.projectCardTitle {
  @apply text-2xl font-roboto;
}
.projectCardDescription {
  @apply text-base font-roboto;
}
.projectCardDescriptionCircle {
  list-style: circle inside;
}
.projectCardFooter {
  @apply text-sm font-roboto text-lightestGray;
}

.blogTitleText {
  @apply font-hateyourwriting text-8xl text-paleTurquoise tracking-wide font-normal;
}
.smalltxt {
  @apply font-winkle text-3xl text-white hover:text-lightestGray duration-200;
}
.blogCard {
  @apply text-white;
}
.blogCardHeaderDate {
  @apply font-roboto text-xs;
}
.blogCardTitleText {
  letter-spacing: 0.15em;
  @apply font-wintersoldier font-extrabold text-4xl;
}
.blogCardTitleTags {
  @apply font-roboto text-xs;
}
.blogCardDescription {
  @apply font-roboto text-base;
}

@media (min-width: 1024px) and (max-width: 1280px) {
  .header-text {
    @apply text-5xl;   
  }
  .subheader-text {
    @apply text-3xl;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .header-text {
    @apply text-4xl;   
  }
  .subheader-text {
    @apply text-2xl;
  }
}
@media (min-width: 420px) and (max-width: 780px) {
  .header-text {
    @apply text-3xl;   
  }
  .subheader-text {
    @apply text-2xl;
  }
  .aboutProjectsTitleText {
    @apply text-7xl;
  }
  .aboutProjectsGithubText {
    @apply text-3xl;
  }
  .aboutTitleText {
    font-size: 3.8rem;
  }
}
@media (max-width: 420px) {
  .header-text {
    @apply text-3xl;   
  }
  .subheader-text {
    @apply text-2xl;
  }
  .aboutProjectsTitleText {
    @apply text-6xl;
  }
  .aboutProjectsGithubText {
    @apply text-3xl;
  }
  .aboutTitleText {
    font-size: 3.5rem;
  }
  .smalltxt {
    @apply text-2xl;
  }
}

